<template>
  <div class="list">
    <el-header>
      <el-select v-model="newsType" placeholder="请选择类型">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary" @click="findTypeNews(newsType)">查询</el-button>
      <!-- Form  新闻类型表单 -->
      <el-button type="primary" @click="FormVisible = true">添加类型</el-button>

      <el-dialog title="编辑" :visible.sync="FormVisible">
        <el-form :model="formData">
          <el-form-item label="新闻类型" :label-width="formLabelWidth">
            <el-input v-model="formData.label" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="类型值" :label-width="formLabelWidth">
            <el-input v-model="formData.value" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="FormVisible = false">取 消</el-button>
          <el-button type="primary" @click="addNewsType">确 定</el-button>
        </div>
      </el-dialog>
      <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <span>设置</span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>查看</el-dropdown-item>
          <el-dropdown-item>新增</el-dropdown-item>
          <el-dropdown-item>删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <el-main>
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        @selection-change="selectionChange"
      >
        <el-table-column
          prop="select"
          label=""
          width="50"
          show-overflow-tooltip
          type="selection"
        >
        </el-table-column>
        <el-table-column
          prop=""
          label="#"
          type="index"
          :index="formatter"
          width="50"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="title"
          label="标题"
          width="220"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="发布时间"
          width="180"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="type"
          label="新闻类别"
          width="150"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <el-tag type="info" v-if="scope.row.type == 1">轮播图</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 2">信工新闻眼</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 3">政治学习</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 4">党建一点通</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 5">党员亮身份</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 6">随时随地学</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 7">随时随地拍</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 8">制度建设</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 9">特色活动</el-tag>
            <el-tag type="info" v-if="scope.row.type  == 10">通知早知道</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="edit(scope.row._id)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="del(scope.row._id)">删除</el-button>
            <!-- Form  新闻类型表单 -->
            <el-button
              size="mini"
              type="success"
              @click="FormVisibleFirst = true ;changeImg(scope.row._id)">生成轮播图</el-button>
            <el-dialog title="添加轮播图" :visible.sync="FormVisibleFirst">
              <el-form :model="formDataFirst">
                <el-form-item label="标题">
                  <el-input v-model="formDataFirst.title" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="跳转链接" >
                  <el-input v-model="formDataFirst.url" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="优先级" >
                  <el-input v-model="formDataFirst.sort" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="状态" >
                  <el-switch
                    v-model="formDataFirst.isShow"
                    active-color="#13ce66"
                    inactive-color="#ff4949">
                  </el-switch>
                </el-form-item>
                <el-form-item label="图片" >
                  <div class="upload">
                  <el-upload
                  class="avatar-uploader"
                  action="https://upload-z1.qiniup.com"
                  :show-file-list="false"
                  :on-success="upload"
                  :data="{token:token}"
                  >
                  <img v-if="formDataFirst.img" :src="formDataFirst.img" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  </el-upload>
                  </div>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="FormVisibleFirst = false">取 消</el-button>
                <el-button type="primary" @click="addCarousel">确 定</el-button>
              </div>
            </el-dialog>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <el-button type="primary">批量重置密码</el-button>
      <el-pagination
        background
        layout="prev, pager, next,jumper"
        :total="total"
        :current-page="page"
        :page-size="pageSize"
        @current-change="pageChange"
      >
      </el-pagination>
    </el-footer>
  </div>
</template>
<script>
  export default{
    name: "news",
    data(){
      return{
        //选择器
        newsType:'',
        options:[],
        //表格
        tableData:[],
        //新闻类型表单
        FormVisible: false,
        formData: {
          label:'',
          value:''
        },
        formLabelWidth: '120px',
        //新闻生成轮播图表单
        formDataFirst: {
          title: '',
          img:'',
          url:'',
          sort:'',
          isShow:'',
        },
        FormVisibleFirst:false,

        token:'',
        //选择器
        multiSelection:[],
        userId:[],
        //分页器
        type:undefined,
        pageSize:5,
        page:1,
        total:5
      }
    },
    methods:{
      //获取新闻类型
      getType(){
        this.$axios.get('getNewsCate').then( res => {
          this.options = res.data
//          console.log(this.options)
        })
      },
      //查询 指定类型的新闻
      findTypeNews(type){
          this.type = type
//        console.log(type)
          this.$axios.get('getNews',{type}).then( res => {
          this.tableData = res.data
        })
      },
      //添加新闻类型
      addNewsType() {
        this.$axios.post('addNewsCate',this.formData).then( res => {
          if(res.code == 200){
            this.$message({
              type:'success',
              message:'添加成功'
            });
          }else{
            this.$message({
              type:'error',
              message:'删除失败'
            });
          }
          setTimeout(() => {
            this.$router.go(0)
          },100)
        })
      },
      //获取所有新闻
      getNews(){
        this.$axios.get('getNews').then( res => {
          this.tableData = res.data
//          console.log(res.data)
        })
      },
      //添加轮播图
      getToken(){
        this.$axios.qiniuGet().then( res => {
          this.token =res.data
        })
      },
      //数据回传
      upload(res){
        this.formData.img = res.url
      },
      changeImg(id){
          this.$axios.get('getNews',{id}).then( res => {
            this.formDataFirst = res.data
          })
      },
      addCarousel(){
          this.$axios.post('addCarousels', this.formDataFirst).then( res => {
            if(res.code == 200){
              this.$message({
                type:'success',
                message:'添加成功'
              });
            }else{
              this.$message({
                type:'error',
                message:'删除失败'
              });
            }
            setTimeout(() => {
              this.$router.go(0)
            },100)
          })
      },
      //编辑新闻
      edit(id){
//        console.log(id)
        this.$router.push({path:'/news/update',query:{id} })
      },
      //删除新闻
      del(id){
        this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.post('delNews', {id}).then(res => {
            if(res.code == 200){
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              setTimeout( () => {
                this.$router.go(0)
              },1000)
            }else{
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          })
        }).catch(() => {
          this.$message({
            type: 'warning',
            message: '已取消删除'
          });
        })
      },
      //批量选择
      selectionChange(val){
        this.multiSelection = val
//          console.log(val)
      },
      //底部分页器
      //这个时候利用formatter就可以实现索引累加啦
      formatter(index){
        //放回索引
        return index + (this.page -1) *this.pageSize +1
      },
      pageChange(val){
//          console.log(`当前页: ${val}`);
        this.getNumber();
        this.page = val
        this.$axios.get('getNews',{page:val, pageSize:this.pageSize,type: this.type}).then( res => {
//              setTimeout( () => {
//                this.$router.go(0)
//              },1000)
          this.tableData = res.data
        })
      },

      //获取数据库文档总数
      getNumber(){
        this.$axios.get('getNewsNum',{type:this.type}).then( res => {
            console.log(this.type)  //没传type,type=""
          this.total = res.data
        })
      }
    },
    created(){
        this.getType();
        this.getNews();
        this.getToken();
        this.getNumber();
    }
  }
</script>
<style>
  .list {
    width: 100%;
    padding-top: 2px;
    padding-left: 20px;
  }
  /*头部*/
  .list .el-header{
    position: relative;
    height: 78px !important;   /*element.style里的样式需要用!important来修改*/
    margin-right: 22px;
    padding-left:11px;
    text-align: left;
    background-color: #f1f1f1;
  }
  .list .el-header .el-input{
    width: 190px;
    margin-top: 10px;
  }
  .list .el-header .el-input .el-input__inner{
    border:1px solid #c2cdda;
  }
  .list .el-header .el-button{
    margin-left:14px;
  }
  .list .el-header .el-dropdown{
    position:absolute;
    top:10px;
    right:10px;
    font-size:22px;
  }
  .list .el-header .el-dropdown i{
    font-size:22px;
    margin:0 !important;
  }
  .list .el-header .el-dropdown span{
    font-size:14px;
    line-height: 26px;
  }
  /*内容区*/
  .list .el-main{
    margin:10px 22px 10px 0;
    padding: 0 14px;
    border:1px solid #dfe7ed;
  }
  .list .el-table .has-gutter .cell{
    color:#000;
  }
  /*弹框*/
  .list .el-header .el-form-item .el-input{
    margin: 0;
  }
  /*表单上传*/
  .upload{
    margin-left: 78px;
  }
  .upload span{
    padding-right: 12px;
  }
  .avatar-uploader{
    display: inline-block;
  }
  .avatar-uploader .el-upload{
    width: 178px;
    height: 178px;
    border:1px solid #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
  }
  .avatar-uploader-icon{
    font-size: 28px;
    color: #8c939d;
    width: 190px;
    height: 190px;
    line-height: 190px;
    text-align: center;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  /*设置上传图片的大小*/
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  /*底部*/
  .list .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .list .el-button{
    margin:10px 0 0 10px;
  }
  .list .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .list .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>



